
@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,700");
html,body{height:100%;overflow-x:hidden
}
body{margin:0 auto;font-size:16px
}
.m__background{position:fixed;content:"";height:100%;width:100%;z-index:-1;height:100%;background-image:url("../assets/background.jpg");background-repeat:no-repeat;background-size:cover;background-position:50% 50%
}
.m__background:after{position:absolute;top:0;content:"";height:100%;width:100%;background-color:rgba(6,214,160,0.9)
}
h1,h2,h3,h4,h5{margin:0
}
ul,ol{margin:0;padding:0
}
.m__inbox{position:relative;background:#fff;width:100%;height:100%;min-height:calc(100vh - 50px)
}
@media (min-width: 680px){
.m__inbox{min-height:calc(100vh - 60px)
}
}
.m__inbox__container{max-width:680px;margin:0 auto;padding:2rem;overflow-y:scroll;-webkit-overflow-scrolling:touch
}
.m__inbox__container::-webkit-scrollbar{display:none
}
.m__inbox__list{padding:0;margin:0;width:100%;font-family:"Lato",sans-serif
}
.m__inbox__list__item{list-style:none;display:-ms-flexbox;display:flex;width:100%;margin:2rem 0
}
.m__inbox__list__item__container{display:-ms-flexbox;display:flex
}
.m__inbox__list__item__container:hover{cursor:pointer
}
.m__inbox__list__item:first-child{margin:0 0 2rem 0
}
.m__inbox__list__item--dot{width:30px;position:relative
}
.m__inbox__list__item--dot--circle{position:absolute;top:50%;transform:translateY(-50%);height:10px;width:10px;background:#bfbfbf;border-radius:50%
}
.m__inbox__list__item--dot--circle--unread{background:#06d6a0
}
.m__inbox__list__item--info{width:calc(100% - 30px)
}
.m__inbox__list__item__name{font-size:1.0rem;color:#4d4d4d;letter-spacing:0.05rem
}
.m__inbox__list__item__copy{font-size:0.75rem;margin:0.5rem 0;color:#bfbfbf;letter-spacing:0.01rem
}
.m__chat{top:0;position:absolute;height:100vh;width:100%;background:#f2f2f2;z-index:+3;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;right:-100%
}
.m__chat__header{position:relative;padding:1rem;height:50px;width:calc(100% - 2rem);display:-ms-flexbox;display:flex;background:#bfbfbf;font-family:"Lato",sans-serif;color:#fff
}
.m__chat__header__arrow{width:50px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;outline:none;background:none;border:none
}
.m__chat__header__arrow:hover{cursor:pointer
}
.m__chat__header__arrow svg{height:25px;width:25px
}
.m__chat__header__name{width:calc(100% - 50px - 3rem);-ms-flex-pack:center;justify-content:center;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;text-align:center;text-transform:uppercase;font-size:1rem;letter-spacing:0.1rem;padding:2rem 1rem
}
.m__chat__messages{height:calc(100% - 50px - 50px - 2rem - 2rem - 2rem);padding:1rem 2rem;overflow-y:scroll;-webkit-overflow-scrolling:touch;position:relative;font-family:"Lato",sans-serif
}
.m__chat__messages::-webkit-scrollbar{display:none
}
.m__chat__messages__message{padding:0.5rem 0
}
.m__chat__messages__message--opposite{text-align:right
}
.m__chat__messages__message--opposite .m__chat__messages__message__text{background:#06d6a0;color:#fff;border-radius:10px 0px 10px 10px
}
.m__chat__messages__message__time{display:block;font-size:0.75rem;margin-bottom:0.25rem;color:#bfbfbf
}
.m__chat__messages__message__text{padding:0.75rem;background:#fff;color:#4d4d4d;border-radius:0px 10px 10px 10px;display:inline-block;width:auto
}
.m__chat__input{width:100%;height:50px;display:-ms-flexbox;display:flex
}
.m__chat__input__text{background:#fff;padding:2rem 1rem;width:calc(100% - 50px - 2rem);height:calc(100% - 2rem);font-family:"Lato",sans-serif;font-size:0.75rem;color:#4d4d4d;border:none
}
.m__chat__input__text:focus{outline:none
}
.m__chat__input__send{padding:1rem;padding:1rem;width:50px;height:100%;background:#06d6a0;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.m__chat__input__send:hover{cursor:pointer
}
.m__chat__input__send svg{height:30px;width:30px
}



