
@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,700");
html,body{height:100%
}
body{margin:0 auto;font-size:16px
}
.m__background{position:fixed;content:"";height:100%;width:100%;z-index:-1;height:100%;background-image:url("../assets/background.jpg");background-repeat:no-repeat;background-size:cover;background-position:50% 50%
}
.m__background:after{position:absolute;top:0;content:"";height:100%;width:100%;background-color:rgba(6,214,160,0.9)
}
h1,h2,h3,h4,h5{margin:0
}
ul,ol{margin:0;padding:0
}
.m__login{background:#fff;position:absolute;z-index:+2;height:calc(100vh - 50px);width:100%
}
.m__login__heading{font-family:"Lato",sans-serif;text-transform:uppercase;background:transparent;font-size:1rem;border:none;color:#4d4d4d;text-align:center;padding-bottom:2rem
}
.m__login__no-account{font-family:"Lato",sans-serif;color:#bfbfbf;font-size:0.75rem;text-align:center;margin-bottom:2rem;text-decoration:none
}
.m__login__error{font-family:"Lato",sans-serif;color:#ef476f;font-size:0.75rem;text-align:center
}
@media (min-width: 680px){
.m__login{height:calc(100vh - 60px)
}
}
.m__login__x{height:40px;width:40px;right:1rem;top:1rem;position:absolute;border:none;background:transparent
}
.m__login__x svg path{fill:#bfbfbf
}
.m__login__x:hover{cursor:pointer
}
.m__login__x:hover svg path{fill:#4d4d4d
}
.m__login__form{max-width:350px;height:calc(100vh - 50px);display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;margin:0 auto
}
@media (min-width: 680px){
.m__login__form{height:calc(100vh - 60px)
}
}
.m__login__form__input{font-family:"Lato",sans-serif;text-transform:uppercase;background:transparent;font-size:1rem;border:none;color:#bfbfbf;border-bottom:solid 1px #bfbfbf;margin:3rem 0;padding:0 0 2rem 2rem
}
.m__login__form__input:last-child{margin-bottom:1rem
}
.m__login__form__input--error{border-bottom:solid 1px #ef476f
}
.m__login__form__input:focus{color:#4d4d4d;border-bottom:solid 1px #4d4d4d;outline:0
}
.m__login__form__input::-webkit-input-placeholder{color:#bfbfbf
}
.m__login__form__radio{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row
}
.m__login__form__radio input{display:none
}
.m__login__form__radio__item{-ms-flex-preferred-size:50%;flex-basis:50%;color:#bfbfbf;text-align:center;padding:1rem 2rem;font-family:"Lato",sans-serif;text-transform:uppercase;font-size:1rem;transition:background 0.3s ease-in
}
.m__login__form__radio__item:first-child{margin:1rem 1rem 1rem 0
}
.m__login__form__radio__item:last-child{margin:1rem 0 1rem 1rem
}
.m__login__form__radio__item:hover{cursor:pointer;background:#f2f2f2
}
.m__login__form__radio__item--active{color:#4d4d4d;border:solid 1px #4d4d4d;background:transparent
}
.m__login__form__radio__item--active:hover{background:transparent
}
.m__login__form__go{height:40px;width:40px;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;background:transparent;outline:0;border:none;transition:transform 0.3s ease-in
}
.m__login__form__go:hover{cursor:pointer;transform:translateX(0.5rem)
}
.m__login__form__go:hover svg path{fill:#eb184a
}
.m__login__form__go svg path{fill:#ef476f
}



